/**
 * Generated by "@vuepress/plugin-blog"
 */
export default {
  post: (prev, next) => {
        const dayjs = require('dayjs');
        const prevTime = dayjs(prev.frontmatter.date);
        const nextTime = dayjs(next.frontmatter.date);
        return prevTime - nextTime > 0 ? -1 : 1;
      },
}